.slider-container {
  overflow: hidden;
  width: 100%;
  position: relative;
  cursor: pointer;
}

.slider {
  display: flex;
  height: 220px;
  transition: transform 0.3s ease;

}

.slide {
  flex: 0 0 85%;
}

.cart-items {
  position: absolute;
  top: 36px;
  display: grid;
  width: 100%;
  transform: translate(0, -100%);
  opacity: 0;
  transition: all 0.3s ease;
  z-index: 2;

  &.active {
    height: auto;
    position: absolute;
    transform: translate(0, 0%);
    opacity: 1;
    transition: all 0.5s ease;

  }
}

.prev-slide,
.next-slide {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: transparent;
  border: none;
}

.prev-slide {
  left: 0;
}

.next-slide {
  right: 0;
}

.clear-cart {
  padding: 0;
  background: none;
  cursor: pointer;
  border: none;
  font-family: "Inconsolata";
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 24px; /* 150% */
  letter-spacing: 0.5px;
  color: #CBC9C8;
  -webkit-tap-highlight-color: transparent;
}
.btn {
  display: flex;
  padding: 0;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  background: none;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  color: #F2F2F2;
  -webkit-tap-highlight-color: transparent;
  &:hover {
    background-color: rgba(242, 242, 242, 0.08);
  }

  &:focus {
    background-color: rgba(242, 242, 242, 0.12);
  }

  &.chevron-down {
    width: 100%;
    height: 24px;
    background: none;
    &:hover {
      background: none;
    }
    &:focus {
      background: none;
    }
    &.active {
      transform: rotate(180deg);
    }
  }
}
